<template>
  <div class="scenario-detail">
    <div v-if="loading" class="text-center my-5">
      <div class="spinner-border text-primary" role="status">
        <span class="visually-hidden">로딩 중...</span>
      </div>
      <p class="mt-3">시나리오를 불러오는 중입니다...</p>
    </div>
    
    <div v-else-if="error" class="alert alert-danger my-5">
      {{ error }}
    </div>
    
    <div v-else-if="!currentScenario" class="alert alert-warning my-5">
      시나리오를 찾을 수 없습니다.
    </div>
    
    <div v-else>
      <div class="d-flex justify-content-between align-items-center mb-4">
        <h1>{{ currentScenario.title }}</h1>
        <div>
          <router-link :to="{ name: 'ScenarioList' }" class="btn btn-outline-secondary me-2">
            목록으로
          </router-link>
          <router-link 
            v-if="isLoggedIn" 
            :to="{ name: 'ScenarioModify', params: { id } }" 
            class="btn btn-primary"
          >
            시나리오 수정
          </router-link>
        </div>
      </div>
      
      <div class="card mb-4">
        <div class="card-header bg-primary text-white">
          <h2 class="h5 mb-0">시나리오 정보</h2>
        </div>
        <div class="card-body">
          <div class="row">
            <div class="col-md-6 mb-3">
              <h3 class="h6">주요 질병</h3>
              <p>{{ currentScenario.main_disease }}</p>
            </div>
            
            <div class="col-md-6 mb-3">
              <h3 class="h6">작성자</h3>
              <p>{{ currentScenario.created_by ? currentScenario.created_by.username : '알 수 없음' }}</p>
            </div>
          </div>
          
          <div class="mb-3">
            <h3 class="h6">기본 인적 사항</h3>
            <div class="table-responsive">
              <table class="table table-bordered">
                <tbody>
                  <tr v-for="(value, key) in currentScenario.personal_info" :key="key">
                    <th style="width: 150px">{{ formatKey(key) }}</th>
                    <td>{{ formatValue(value) }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          
          <div v-if="currentScenario.additional_info" class="mb-3">
            <h3 class="h6">기타 사항</h3>
            <p>{{ currentScenario.additional_info }}</p>
          </div>
          
          <div class="mb-3">
            <h3 class="h6">생성일</h3>
            <p>{{ formatDate(currentScenario.created_at) }}</p>
          </div>
          
          <div class="mb-3">
            <h3 class="h6">최종 수정일</h3>
            <p>{{ formatDate(currentScenario.updated_at) }}</p>
          </div>
        </div>
      </div>
      
      <div class="card">
        <div class="card-header bg-light">
          <h2 class="h5 mb-0">시나리오 내용</h2>
        </div>
        <div class="card-body">
          <div class="scenario-content">
            <pre class="scenario-text">{{ currentScenario.content }}</pre>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'ScenarioDetail',
  props: {
    id: {
      type: [String, Number],
      required: true
    }
  },
  computed: {
    ...mapGetters(['currentScenario', 'isLoading', 'error', 'isLoggedIn'])
  },
  methods: {
    formatKey(key) {
      const keyMap = {
        gender: '성별',
        age: '나이',
        medical_history: '과거력',
        family_history: '가족력',
        allergies: '알레르기',
        vital_signs: '활력징후',
        symptoms: '주요증상'
      }
      
      return keyMap[key] || key
    },
    formatValue(value) {
      if (Array.isArray(value)) {
        return value.join(', ')
      }
      return value
    },
    formatDate(dateString) {
      if (!dateString) return '알 수 없음'
      
      const date = new Date(dateString)
      return new Intl.DateTimeFormat('ko-KR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      }).format(date)
    }
  },
  async created() {
    // 시나리오 정보 로드
    if (!this.currentScenario || this.currentScenario.id !== parseInt(this.id)) {
      await this.$store.dispatch('fetchScenario', this.id)
    }
  }
}
</script>

<style scoped>
.scenario-detail {
  max-width: 900px;
  margin: 0 auto;
}

.scenario-content {
  background-color: #f8f9fa;
  border-radius: 5px;
  padding: 20px;
}

.scenario-text {
  white-space: pre-wrap;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  margin: 0;
}

.card {
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.table th {
  background-color: #f8f9fa;
}
</style> 