import { createStore } from 'vuex'
import axios from 'axios'
import { getAuth, signInWithEmailAndPassword, createUserWithEmailAndPassword, signOut, onAuthStateChanged } from 'firebase/auth'

// API 기본 URL 설정
axios.defaults.baseURL = process.env.VUE_APP_API_URL || 'http://localhost:8000'

export default createStore({
  state: {
    user: null,
    scenarios: [],
    currentScenario: null,
    loading: false,
    error: null
  },
  getters: {
    isLoggedIn: state => !!state.user,
    currentUser: state => state.user,
    isAdmin: state => state.user && state.user.is_admin,
    scenarios: state => state.scenarios,
    currentScenario: state => state.currentScenario,
    isLoading: state => state.loading,
    error: state => state.error
  },
  mutations: {
    SET_USER(state, user) {
      state.user = user
    },
    SET_SCENARIOS(state, scenarios) {
      state.scenarios = scenarios
    },
    SET_CURRENT_SCENARIO(state, scenario) {
      state.currentScenario = scenario
    },
    SET_LOADING(state, loading) {
      state.loading = loading
    },
    SET_ERROR(state, error) {
      state.error = error
    },
    CLEAR_ERROR(state) {
      state.error = null
    }
  },
  actions: {
    // 인증 상태 확인
    checkAuth({ commit }) {
      const auth = getAuth()
      onAuthStateChanged(auth, async (user) => {
        if (user) {
          try {
            // Firebase 인증 후 백엔드에서 사용자 정보 가져오기
            const token = await user.getIdToken()
            const response = await axios.get('/api/users/me/', {
              headers: {
                'Authorization': `Bearer ${token}`
              }
            })
            commit('SET_USER', response.data)
          } catch (error) {
            console.error('사용자 정보 가져오기 실패:', error)
            commit('SET_USER', null)
          }
        } else {
          commit('SET_USER', null)
        }
      })
    },
    
    // 로그인
    async login({ commit }, { email, password }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const auth = getAuth()
        const userCredential = await signInWithEmailAndPassword(auth, email, password)
        const token = await userCredential.user.getIdToken()
        
        // 백엔드에서 사용자 정보 가져오기
        const response = await axios.get('/api/users/me/', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        })
        
        commit('SET_USER', response.data)
        commit('SET_LOADING', false)
        return response.data
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 회원가입
    async register({ commit }, { username, email, password }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const auth = getAuth()
        const userCredential = await createUserWithEmailAndPassword(auth, email, password)
        const token = await userCredential.user.getIdToken()
        
        // 백엔드에 사용자 정보 등록
        const response = await axios.post('/api/users/', {
          username,
          email,
          firebase_uid: userCredential.user.uid
        }, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        })
        
        commit('SET_USER', response.data)
        commit('SET_LOADING', false)
        return response.data
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 로그아웃
    async logout({ commit }) {
      try {
        const auth = getAuth()
        await signOut(auth)
        commit('SET_USER', null)
      } catch (error) {
        commit('SET_ERROR', error.message)
        throw error
      }
    },
    
    // 시나리오 목록 가져오기
    async fetchScenarios({ commit }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const response = await axios.get('/api/scenarios/')
        commit('SET_SCENARIOS', response.data)
        commit('SET_LOADING', false)
        return response.data
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 시나리오 상세 정보 가져오기
    async fetchScenario({ commit }, id) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const response = await axios.get(`/api/scenarios/${id}/`)
        commit('SET_CURRENT_SCENARIO', response.data)
        commit('SET_LOADING', false)
        return response.data
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 시나리오 생성
    async createScenario({ commit, getters }, scenarioData) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const auth = getAuth()
        const token = await auth.currentUser.getIdToken()
        
        const response = await axios.post('/api/scenarios/generate/', scenarioData, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        })
        
        commit('SET_LOADING', false)
        return response.data
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 시나리오 수정
    async modifyScenario({ commit, getters }, { scenarioId, userInput }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const auth = getAuth()
        const token = await auth.currentUser.getIdToken()
        
        const response = await axios.post('/api/modifications/modify/', {
          scenario_id: scenarioId,
          user_input: userInput
        }, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        })
        
        // 현재 시나리오 업데이트
        if (getters.currentScenario && getters.currentScenario.id === scenarioId) {
          const scenarioResponse = await axios.get(`/api/scenarios/${scenarioId}/`)
          commit('SET_CURRENT_SCENARIO', scenarioResponse.data)
        }
        
        commit('SET_LOADING', false)
        return response.data
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    }
  }
}) 